        //   },
        //   callback: function() {
        //     location.href = 'itms-apps://itunes.apple.com/app/id544673497'
        //   }
        // })
        _app(base64Info)
        location.href="itms-apps://itunes.apple.com/app/id544673497"
        console.log(`app base64info ${base64Info}`);
        console.log(`goAppInfo ${atob(base64Info)}`);

      }
    }
  }
</script>
<style>
  .testBtn {
    width: 100px;
    height: 40px;
    line-height: 40px;
    font-size: 18px;
